import React from "react";

export default function Contact() {
    return (
        <div className="size">
            <section className="base-container bg-tertiary">
                <div className="contact-icon"></div>
                <div className="pages-border"></div>
                <div className="pages-banner">
                    <div className="pages-banner__headline contact-shake">
                        C O N T A C T
                    </div>
                    <div className="pages-banner__sub-headline">
                        FEEL FREE TO LEAVE A MESSAGE
                    </div>
                </div>
            </section>
            <section className="base-container--reverse bg-tertiary">
                <div className="pages-border"></div>
                <div className="pages-content">
                    <div className="pages-content__headline">Contact me?</div>
                    <div className="pages-content__sub-headline">
                        If you wish to contact me
                    </div>
                    <div className="pages-content__sub-headline">
                        Hit me up on{" "}
                        <span
                            onClick={openInsta}
                            role="img"
                            aria-label="camera"
                            class="contact-social-media"
                        >
                            üì∏
                        </span>
                    </div>
                    <div className="pages-content__sub-headline">
                        or click{" "}
                        <span
                            onClick={mailMe}
                            class="contact-social-media"
                            role="img"
                            aria-label="envelope"
                        >
                            ‚úâÔ∏è
                        </span>{" "}
                        to mail me personally
                    </div>
                    <div className="pages-content__sub-headline">
                        All Icons used are from flaticon.com/Freepik
                    </div>
                </div>
            </section>
        </div>
    );
}

var openInsta = () => {
    window.open("https://www.instagram.com/revanjin", "_blank");
};

var mailMe = () => {
    window.location.href = "mailto:alex.tanachai@hotmail.de";
};
